@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "helpers/normalize";

* {
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

html {
  font-family: $got;

  --title-spacing: 100px;

  @include temp {
    --title-spacing: 60px; }

  @include xs {
    --title-spacing: 0; } }

body {
  width: 100%;
  background: #fff;
  color: $black;
  font-family: $got;
  font-size: 16px;
  line-height: 1.36; }

a {
  color: #18db73;
  transition: all linear .2s;

  &:hover {
    text-decoration: none; } }

.clearfix:after {
  clear: both;
  display: block;
  visibility: hidden;
  height: 0;
  content: ".";
  font-size: 0; }

.clearfix {
  zoom: 1; }

.ir {
  font: 0/0 a;
  text-shadow: none;
  color: transparent; }

.out {
  position: relative;
  overflow: hidden;
  min-height: 100%;
  min-width: 300px; }

.container {
  position: relative;
  max-width: 1220px;
  margin: 0 auto;
  padding: 130px 20px;

  @include sm {
    padding: 65px 20px; } }

.bg-black {
  background: $black;
  color: $white;

  & + .bg-black {
    .container {
      padding-top: 0;

      @include sm {
        padding-top: 0; } } } }

.d-block {
  display: block; }

.d-flex {
  display: flex; }

.d-none {
  display: none; }

.my-0 {
  margin-top: 0;
  margin-bottom: 0; }

.mt-0 {
  margin-top: 0; }

.mb-0 {
  margin-bottom: 0; }

.py-0 {
  padding-top: 0;
  padding-bottom: 0; }

.pt-0 {
  padding-top: 0; }

.pb-0 {
  padding-bottom: 0; }

#popup-ticket {

  .pop-ticket {

    &__btn {
      color: $white;
      background: transparent;
      border: 2px solid $themeColor;

      &:hover {
        background: $themeColor;
        color: $black; }

      &:after,
      &:before {
        display: none; } }

    &__input {
      outline: none;
      border: none; }

    &__input_btn {
      color: $white;
      background: $black;
      border: 2px solid $themeColor;
      cursor: pointer;
      transition: all ease 0.1s;

      &:hover {
        background: $themeColor;
        color: $black;

        svg {
          fill: $black; } } } } }




