.sponsors {
  .container {
    padding-bottom: 0; }

  .section {
    &__desc {
      a {
        font-weight: 700; } } } }

.sponsors-block {
  margin: 130px 0 50px;
  @include md {
    margin: 75px 0 50px; }
  @include sm {
    margin: 48px 0 50px; }

  &__item {
    text-align: center; }
  & + & {
    margin-top: 50px; }
  &_xl {
    .sponsors-block__item {
      max-width: 50%;
      width: 33.33%;
      padding: 25px 30px;
      @include md {
        padding: 20px;
        img {
          max-width: 100%; } }
      @include md {
        width: 50%; }
      @include sm {
        width: 75%;
        max-width: 100%;
        padding: 20px 10px; } } }
  &_lg {
    //.sponsors-block__list
    //margin: 0 -50px
    //@include md
    //  margin: 0 -20px
    //@include xs
    //  margin: 0 -10px
    .sponsors-block__item {
      padding: 25px 30px;
      max-width: 50%;
      width: 33.33%;
      img {
        max-width: 100%; }
      @include md {
        padding: 20px; }
      @include md {
        width: 50%; }

      @include xs {
        padding: 7px; }
 } }      //max-width: 217px
  &_md {
    .sponsors-block__list {
      margin: 0 -50px;
      @include md {
        margin: 0 -20px; }
      @include xs {
        margin: 0 -10px; } }
    .sponsors-block__item {
      padding: 25px 30px;
      width: 33.33%;
      img {
        max-width: 100%; }
      @include md {
        padding: 20px; }
      @include xs {
        padding: 5px; }
      //max-width: 193px
 } }      //width: 100%
  &_sm {
    .sponsors-block__list {
      margin: 0 -25px; }
    .sponsors-block__item {
      padding: 20px 25px;
      width: 25%;
      img {
        max-width: 100%; }
      @include md {
        padding: 20px; }

      @include md {
        width: 33.33%; }
      //+md
      //width: 50%
      //img
 } }      //  max-width: 100%

  &_xs {
    .sponsors-block__list {
      margin: 0 -15px; }
    .sponsors-block__item {
      width: 20%;
      a {
        padding: 20px 25px; }
      img {
        max-width: 100%; }
      @include md {
        a {
          padding: 20px; } }

      @include md {
        width: 25%; }

      @include xs {
        width: 33.3%;
        a {
          padding: 15px 10px; } } } }

  &_auto {
    .sponsors-block__list {
      justify-content: space-between;
      margin: 0 -15px; }
    .sponsors-block__item {
      width: auto;
      a {
        padding: 20px 25px; }
      img {
        max-width: 100%; }
      &:last-child {
        margin: 0 auto 0 0; }
      @include md {
        a {
          padding: 20px; } }

      @include sm {
        width: 25%;
        a {
          padding: 15px 10px; } }

      @include xs {
        width: 33.3%;
        a {
          padding: 15px 10px; } } } }
  &__list {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    padding: 0;
    list-style: none; }
  &__item {
    @include md {
      display: flex;
      justify-content: center; }
    @include sm {
      max-width: 230px;
      img {
        max-width: 100%; } }
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      transition: 0.3s;

      &:hover {
        opacity: 0.7; }

      img {
        transition: all ease 0.2s;
        transform: translateY(0);
        opacity: 1;
        height: auto; } } }

  &__title {
    position: relative;
    font-size: 30px;
    font-weight: 700;
    width: 100%;
    margin: 0 0 0 5px;

    @include sm {
      font-size: 20px;
      margin-bottom: 32px; }

    &:after {
      content: '';
      position: absolute;
      left: -8px;
      bottom: -5px;
      height: 20px;
      width: 92px;
      background: #24fc89;
      z-index: -1;

      @include sm {
        height: 14px;
        width: 68px;
        bottom: 0; } } } }
