.faq {
  margin: 0 auto;
  max-width: 970px;

  &.marged {
    margin-top: 52px; }

  &__list {
    list-style: none;
    padding: 0;
    margin: 0; }

  &__arrow {
    content: '';
    position: absolute;
    top: 35px;
    right: 42px;
    width: 7px;
    height: 12px;
    transform: rotate(90deg);
    transition: all 0.5s;
    fill: #000; }

  &__item {
    position: relative;
    border: 2px solid $themeColor;
    margin: 0 0 -2px;
    cursor: pointer;
    transition: all ease 0.1s;

    &.open {
      .faq__arrow {
        transform: rotate(-90deg); } } }

  &__question {
    font-size: 22px;
    font-weight: 700;
    padding: 25px 90px 25px 40px;

    p {
      margin: 0; } }

  &__answer {
    font-size: 14px;
    line-height: 1.5;
    display: none;
    padding: 0 40px 25px;
    max-width: 840px;

    p {
      margin: 16px 0 0;

      &:first-child {
        margin: 0; } } }

  @include sm {
    &__arrow {
      top: 24px;
      right: 28px; }

    &__question {
      font-size: 22px;
      font-weight: 700;
      cursor: pointer;
      padding: 15px 60px 15px 20px; }

    &__answer {
      font-size: 14px;
      padding: 0 20px 15px; } }


  @include xs {
    &__question {
      font-size: 20px; } } }
