.remote-switch {
  font-size: 12px;
  position: sticky;
  // left: 50%
  // transform: translateX(-50%)
  top: 12px;
  display: flex;
  height: 58px;
  align-items: center;
  padding: 0;
  background: transparent;
  pointer-events: auto;
  margin: 0 0 0 auto;
  border: 2px solid $black;
  width: max-content;

  @include temp {
    height: 50px;
    margin: 10px auto 0 0;
    // border: 2px solid $themeColor
    // background: $themeColor
 }    // z-index: 9

  &__text {
    opacity: 0.6;
    font-size: 13px;
    text-decoration: none;
    color: $black;
    height: 100%;
    padding: 0 10px;
    display: flex;
    font-weight: 700;
    align-items: center;

    @include temp {
      font-size: 12px; }

    &:first-child {
      padding: 0 10px 0 20px;

      @include temp {
        padding: 0 10px 0 10px; } }

    &:last-child {
      padding: 0 20px 0 10px;

      @include temp {
        padding: 0 10px 0 10px; } }


    &:not(.active):hover {
      color: $black;
      opacity: 1; }

    &.active {
      opacity: 1; } }

  &__switch {
    position: relative;
    width: 36px;
    height: 12px;
    border-radius: 6px;
    background: rgba(0, 0, 0, 0.2);
    cursor: pointer;

    @include temp {
      width: 22px;
      height: 10px; }

    &:after {
      content: '';
      position: absolute;
      left: -3px;
      top: -3px;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      background: $black;

      @include xs {
        width: 14px;
        height: 14px;
        top: -2px;
        left: -2px; } }

    &.active {
      &:after {
        left: auto;
        right: -3px;

        @include xs {
          left: auto;
          right: -2px; } } } } }
