.header {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  transition: all .3s ease;
  z-index: 10;

  .container {
    display: flex;
    justify-content: space-between;
    padding: 20px 20px;
    flex: 1; }

  &.live {
    top: auto;
    bottom: 0;
    opacity: 0; }

  &.sticky,
  &.visible {
    background: $themeColor;
    opacity: 1;

    .logo-mini {
      opacity: 1;
      transform: rotate(0) scale(1); }

    .socials {
      &__link {
        fill: $themeColor;

        &:hover {
          .socials__img {
            fill: $white; } } } }
    @include temp {
      opacity: 0;
      visibility: hidden; } }

  &__logo {
    margin: 0 30px 0 0; }

  &__nav {
    flex: 1 0 auto; }

  &__socials {
    margin: 0 0 0 14px;
    display: flex;
    flex-wrap: wrap;

    @include temp {
      flex-direction: column; } }

  @include temp {
    transition: all .3s ease;
    height: 62px;
    overflow: hidden;
    visibility: hidden;

    &.sticky {
      background: $themeColor;

      .logo-mini {
        opacity: 1; } }

    .container {
      flex-direction: column;
      min-height: 100%;
      justify-content: flex-start; }

    .header {
      &__logo {
        order: 1;
        margin: -10px 0 0; }

      &__nav {
        order: 2;
        margin: 0;
        display: flex;
        flex: 0;

        nav {
          margin: 0;
          width: 100%; } }

      &__socials {
        order: 3;
        margin: 40px 0 80px; } }

    .logo-mini {
      position: relative;
      display: block;
      margin: 0 auto;
      padding: 0;
      transform: none;
      left: 0;
      top: 0;
      opacity: 0; }

    .nav {
      flex-direction: column;
      max-width: 300px;
      margin: 0 auto; }

    .socials {
      margin: 0 auto;
      justify-content: center;

      &__item {
        text-align: center;
        margin: 0; } } }

  &__btn {
    margin: -7px 0 -7px 10px;

    @include temp {
      margin: 10px auto 0; }

    &.menu-btn {
      display: none;

      @include temp {
        display: inline-flex; } } }

  &.open {
    @include temp {
      visibility: visible;
      opacity: 1;
      background: $white;
      min-height: 100vh;
      overflow-y: auto; } } }

.logo-mini {
  position: relative;
  margin: 0;
  height: 44px;
  width: 56px;
  opacity: 0;
  transform: rotate(120deg) scale(0.5);
  transition: all 0.5s;

  &__link {
    display: block;
    height: 100%;
    background: url(../img/logo_mini.svg) no-repeat;
    transition: none;
    background-size: 100%; } }

.nav {
  font-size: 14px;
  display: flex;
  padding: 0 10px;
  margin: 0 auto;
  list-style: none;
  font-weight: 700;
  flex: 1 1 auto;

  //justify-content: space-between;

  &__item {
    position: relative;
    margin-right: 10px;

    &:before {
      content: '';
      position: absolute;
      left: -4px;
      top: 50%;
      transform: translateY(-1px);
      height: 2px;
      background: $black;
      width: 12px;
      opacity: 0;
      transition: all .3s ease;
      transform: translateX(-10px); }

    &.active,
    &:hover {
      &:before {
        opacity: 1;
        transform: translateX(0); } } }


  &__link {
    display: flex;
    height: 40px;
    text-decoration: none;
    align-items: center;
    padding: 0 10px;
    color: $black; }

  @include md {
    font-size: 12px; }


  @include temp {
    font-size: 16px; } }

