.menu-toggle {
  position: fixed;
  top: 10px;
  right: 16px;
  width: 40px;
  height: 40px;
  display: none;
  cursor: pointer;
  background: none;
  justify-content: center;
  padding: 0;
  border: none;
  outline: none;
  z-index: 11;

  span {
    display: block;
    width: 24px;
    height: 2px;
    background: $themeColor;
    margin: 10px auto;
    transition: all ease 0.2s; }

  &.open {
    span {
      background: $black;
      &:nth-child(1) {
        transform: translateY(6px) rotate(45deg); }

      &:nth-child(2) {
        transform: translateY(-6px) rotate(-45deg); } } }

  @include temp {
    display: block; } }
