.footer {
	position: relative;
	background: $black;
	color: $white;

	&.live {
		padding-bottom: 85px;

		@include temp {
			padding-bottom: 0; } }

	.container {
		padding: 0 20px; }

	.nav {
		margin: 0 20px;

		&__link {
			color: $themeColor;

			&:hover {} }

		&__item {
			&:after {
				background: $themeColor;
				transform: translateY(-1px); } } }

	@include temp {
		.nav {
			font-size: 14px; } }


	@include md {
		height: auto;

		.nav {
			margin: 0 0 0 -20px; } }


	@include sm {

		.nav {
			display: flex;
			flex-direction: column;
			text-align: center;
			justify-content: center;
			align-items: center;

			&__item {
				margin: 0; } } }


	@include xs {
		.nav {} }


	&__main {
		padding: 90px 0 100px;
		display: flex;
		width: 100%;
		flex-direction: column;
		@include xl {
			padding: 50px 0 70px; }

		.container {
			padding: 0 20px 0 20px;
			display: flex;
			align-items: center;
			height: 100%;
			justify-content: space-between; } }

	&__title {
		font-size: clamp(42px, 6vw, 94px);
		font-weight: 900;
		line-height: 1;
		margin: 0;
		margin-bottom: clamp(32px, 5vw, 62px);
		text-align: left;
		color: $white;

		@include sm {
			text-align: center; } }

	&__mid {
		display: flex;

		@include sm {
			flex-direction: column-reverse;
			align-items: center; } }

	&__basement {
		position: relative;
		margin: 0 auto;
		background-position: top center;
		border-top: 1px solid rgba(255, 255, 255, 0.25);
		display: flex;

		.container {
			padding: 0 160px 0 20px;
			display: flex;
			align-items: center;
			height: 100%;
			justify-content: space-between;
			min-height: 105px;
			width: 100%;

			@include md {
				padding: 15px 180px 15px 20px;
				flex-direction: column;
				align-items: flex-start; }

			@include sm {
				flex-direction: column-reverse;
				padding: 60px 20px 15px 20px;
				align-items: center; } } }

	&__tip {
		line-height: 1.42;
		font-size: 14px;

		a {
			color: $themeColor; }

		@include sm {
			font-size: 12px; } }

	&__socials {
		margin: 0 auto 0 175px;
		margin-bottom: 88px;
		@include xl {
			margin-bottom: 40px; }
		@include md {
			margin-left: 70px; }
		@include sm {
			margin: 0 0 30px;
			order: 0; }

		.social {
			display: flex;
			gap: 12px;
			margin: 0 auto;
			justify-content: center;
			list-style: none;
			padding: 0;

			&.social--theme {
				.social {
					&__item {
						margin: 0; } } }

			&__item {
				margin: 0; }

			&__link {
				width: clamp(56px, 10vw, 78px);
				height: clamp(56px, 10vw, 78px);
				border: 3px solid $themeColor;
				display: flex;
				justify-content: center;
				align-items: center;
				border-radius: 50%;
				transition: all ease 0.1s;
				background: $themeColor;

				&:hover {
					border-color: $themeColor;
					background: $themeColor;

					.icon {
						fill: $black!important; } }

				.icon {
					width: 28px;
					height: 28px;
					fill: $black; } } } } }

.f-logo {
	a {
		text-decoration: none; }

	display: flex;

	&:before {
		content: '';
		display: inline-block;
		width: 40px;
		height: 50px;
		margin: 0 10px 0 0;
		background: url(../img/f-logo.svg) center center no-repeat; }

	&__desc {
		margin: auto 0; }

	&__by {
		font-size: 13px;
		color: $white;
		margin: 6px 0 0;
		line-height: 1.2; }

	&__org {
		position: relative;
		font-size: 25px;
		font-weight: 700;
		color: $themeColor;
		margin: 0;
		left: -5px;
		line-height: 1.2; }

	@include md {
		margin: 10px 0; } }

.f-reserve {
	position: absolute;
	right: 0;
	top: 0;
	bottom: 0;
	height: auto;
	font-size: 14px;

	@include sm {
		display: flex;
		order: 1;
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		width: 100%;
		max-width: 320px;
		top: -20px;
		height: 60px;
		border: 3px solid $black; }


	@include xs {
		max-width: none; } }


.f-nav {
	display: flex;
	flex-direction: column;
	font-size: 16px;
	margin: 0;
	list-style: none;

	@include sm {
		font-size: 14px;
		padding: 0; }

	&__item {
		&:not(:first-child) {
			margin-top: 10px; } }

	&__link {
		color: $white; } }
