.s-prices {
	position: relative;

	.section__title {
		color: $themeColor; }

	&__decor {
		position: absolute;
		top: 35px;
		left: 50%;
		margin: 0 0 0 532px;
		width: 219px;
		height: 209px;
		background: url(../img/decor_ticket.png) center center;
		background-size: cover;
		z-index: 0;

		@include xl {
			left: auto;
			right: -40px;
			margin: 0; }

		@include temp {
			right: 10px;
			width: 110px;
			height: 105px; }

		@include xs {
			top: 0px; } } }

.prices {
	font-size: 14px;

	@include md {
		margin: 0 -20px; }

	&__list {
		position: relative;
		padding: 32px 0 30px;
		margin: 0;
		list-style: none;
		overflow-x: auto;
		display: flex;

		p {
			margin: 0; }

		&::-webkit-scrollbar {
			width: 16px;
			height: 16px;

			@include temp {
				height: 8px; } }

		&::-webkit-scrollbar-button {
			width: 0px;
			height: 0px; }

		&::-webkit-scrollbar-thumb {
			background: #0a0a0a;
			border: 0px solid #fff;
			border-radius: 0px;
			cursor: pointer; }

		&::-webkit-scrollbar-thumb:hover {
			background: #000; }

		&::-webkit-scrollbar-thumb:active {
			background: #000; }

		&::-webkit-scrollbar-track {
			background: #333;
			border: 0px solid #fff;
			border-radius: 0px; }

		&::-webkit-scrollbar-track:hover {
			background: #333; }

		&::-webkit-scrollbar-track:active {
			background: #333; }

		&::-webkit-scrollbar-corner {
			background: transparent; }

		@include md {

			&:after,
			&:before {
				content: '';
				min-width: 20px;
				display: block;
				min-height: 100%; } }

		@include sm {
			padding: 42px 0 10px; } }

	&__item {
		display: flex;
		flex-direction: column;
		flex: 1 1;
		text-align: center;
		padding: 12px 20px 30px;
		min-width: 320px;

		@include sm {
			background: rgba(255, 255, 255, 0.1);
			border-radius: 5px;
			min-width: 294px; }

		&:not(:first-child) {
			margin-left: 20px; } }

	&__title {
		font-size: 22px;
		font-weight: 700;
		line-height: 1.34;
		margin: 20px 0 16px;

		em {
			@include regular;
			font-size: 18px;
			font-style: normal; }

		@include sm {
			margin: 2px 0 16px; } }

	&__price {
		@include medium;
		font-size: 16px;
		color: $white;
		margin: 0 0 24px;

		strong {
			@include medium;
			display: inline-block;
			position: relative;
			font-size: 50px;
			top: 8px;
			color: $themeColor;
			padding: 0 10px;
			margin: 0 5px 0;
			line-height: 1;

			&:after {
				content: '';
				position: absolute;
				left: 0;
				bottom: -3px;
				right: 0;
				height: 22px;
				background: rgba(36, 252, 137, 0.15); }

			em {
				font-style: normal;
				font-weight: 400;
				display: inline-block;
				position: relative;
				font-size: 36px;
				margin: 0 5px 0 0;
				top: -6px; } }

		& + .prices__desc {
			margin-top: 4px; } }

	&__price-tip {
		font-size: 14px;
		color: #898989;

		a {
			color: $white; } }

	&__desc {
		font-size: 14px;
		margin: 22px 0 30px;
		line-height: 1.82;

		p {
			&:not(:first-child) {
				margin: 18px 0 0; } }

		em {
			font-size: 12px;
			line-height: 1.5;
			color: #898989;
			font-style: normal; }

		a {
			color: $white; } }

	&__btn {
		margin: auto auto 0; }

	&__bottom {
		display: flex;
		font-size: 14px;
		padding: 20px 20px;
		margin: 0 auto;
		text-align: center;
		color: $themeColor;
		background: rgba(36, 252, 137, 0.15);
		width: fit-content; } }
