.intro {
  position: relative;
  min-height: 100vh;
  background: url(../img/intro_bg.png) center center no-repeat;
  background-size: cover;

  &.height-auto {
    min-height: 0;

    .container {
      min-height: 0; }

    .intro__content {
      margin: 50px 0; } }

  .container {
    box-sizing: border-box;
    display: flex;
    padding: 80px 20px;
    min-height: 100vh;

    @include temp {
      padding: 20px 10px 20px; }

    @include xs {
      padding: 18px 10px 116px;

      &.single-btn {
        padding-bottom: 58px;

        .intro__important-guys {
          bottom: 82px; }

        .intro__octo {
          margin-top: 240px; } } } }


  &__octo {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(0, -50%);
    margin: 0 0 0 -1110px;
    width: 813px;
    height: 594px;
    background: url(../img/octo.png) center center no-repeat;
    background-size: 100%;

    &-text {
      position: absolute;
      right: 118px;
      top: 89px;
      line-height: 1.54;
      font-size: 14px;
      text-align: center;
      font-weight: 600;
      height: 63px;
      width: 140px;
      display: flex;
      align-items: center;
      justify-content: center; } }

  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    margin: auto 0;
    width: 100%; }

  &__desc {
    max-width: 460px;
    text-align: center;
    margin: 22px auto;
    line-height: 1.72;
    font-size: 14px;
    font-weight: 400; }

  &__buttons {
    position: absolute;
    margin: 0;
    z-index: 1;
    right: 0;
    bottom: -8px;
    display: flex;
    flex-direction: column;

    @include md {
      bottom: 0; }

    @include xs {
      left: 0;
      right: 0;
      background: $white; }

    & > .basic-btn {
      &:not(:first-child) {
        margin-top: 20px;

        @include xs {
          margin-top: 0; } } } }

  &__important-guys {
    position: absolute;
    left: 0;
    bottom: 37px;
    width: 118px;
    height: 55px;
    background: url(../img/focus-reactive.png) center center no-repeat;
    background-size: 100%;
    display: block;
    z-index: 1;

    @include sm {
      width: 75px;
      height: 35px; } }

  @include md {
    &__reserve {
      margin: 112px 0 0; }

    &__octo {
      margin: 0 0 0 -950px; } }

  @include sm {
    &__desc {
      font-size: 13px;
      line-height: 1.4;
      margin: 9px auto; } }

  @include temp {
    &__reserve {
      margin: auto 0 0 0;
      bottom: 4px; }

    &__important-guys {
      bottom: 4px;
      left: 10px; } }

  @include sm {
    &__octo {
      margin: 290px 0 0 -620px; } }

  @include xs {
    &__octo {
      top: 0;
      transform: translateY(0);
      width: 546px;
      height: 426px;
      margin: 240px 0 0 -422px;

      &-text {
        font-size: 10px;
        right: 57px;
        top: 62px; } }

    &__content {
      position: initial;
      margin: 0; }

    &__reserve {
      margin: 0;
      width: 100%;
      position: absolute;
      left: 0;
      bottom: 0;
      right: 0; }

    &__important-guys {
      width: 75px;
      height: 35px;
      bottom: 140px;
      left: 22px; } }

  @media only screen and (min-device-height: 667px) and (max-device-width: 480px) {
    min-height: 0;

    .container {
      min-height: 667px; } } }

.logo {
  height: 266px;
  width: 458px;
  margin: 0 auto 30px;
  position: relative;
  z-index: 1;

  &__link {
    display: block;
    height: 100%;
    background: url(../img/logo.svg) no-repeat;
    transition: none;
    background-size: 100%; }

  &.short {
    height: 109px; }

  &.alt {

    .logo__link {
      background-image: url(../img/logo-alt.svg); } }

  @include md {
    height: 214px;
    width: 368px;

    &.short {
      height: 88px; } }

  @include sm {
    height: 140px;
    width: 238px;
    margin: 38px auto 18px;

    &.short {
      height: 65px; } }

  @include xs {
    margin: 12px auto 18px; } }

.remote-sign {
  position: relative;
  font-weight: 600;
  color: $black;
  margin: -22px auto 18px;
  font-size: 18px;
  padding: 0 8px;
  display: none;

  span {
    font-weight: 500;
    font-size: 42px;
    font-family: $rb; }

  @include sm {
    margin: 6px auto 18px;
    font-size: 14px;
    padding: 0 4px; }

  &:after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 11px;
    background: $themeColor;
    height: 11px;
    z-index: -1; } }

.logo-desc {
  font-family: $rb;
  font-size: 80px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  margin: 24px auto 5px;

  span {
    font-size: 65px; }

  @include md {
    font-size: 72px;

    span {
      font-size: 55px; } }

  @include sm {
    font-size: 50px;

    span {
      font-size: 38px; } } }

.stats {
  padding: 0;
  margin: 42px 0 0;
  list-style: none;
  display: flex;
  justify-content: center;
  position: relative;
  z-index: 1;

  @include md {
    margin: 42px 0 0; }

  @include temp {
    margin: 42px 0 0; }

  @include sm {
    margin: 12px 0 0; }

  &__item {
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-self: flex-start;
    min-width: 180px;

    @include xl {
      min-width: 0;
      width: 140px; }

    @include md {
      min-width: 0;
      width: 110px; }

    @include sm {
      width: 33%; } }

  &__num {
    font-weight: 600;
    font-size: 18px;
    line-height: 1.25;
    margin: 0;
    padding: 0;
    text-align: center;
    display: flex;
    flex-direction: column;

    @include md {
      font-size: 16px; }

    span {
      font-weight: 500;
      font-size: 45px;
      display: inline-block;
      position: relative;
      margin: 0 auto 4px;
      padding: 0 10px;

      @include md {
        font-size: 36px; }

      @include sm {
        margin: 0 auto 0;
        font-size: 24px; }

      &:after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: 4px;
        top: 53%;
        background: $themeColor;
        z-index: -1; } } }

  &__desc {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.36;
    margin: 8px auto 0;
    padding: 0;
    text-align: center;
    min-width: 0;
    max-width: 160px;

    @include md {
      font-size: 14px; }

    @include sm {
      font-size: 10px;
      margin: 0 auto; } } }

.big-day {
  position: absolute;
  right: 0;
  top: 182px;
  font-weight: 500;
  text-align: right;
  z-index: 1;

  &__mon {
    font-size: 45px;

    p {
      margin: 0; }

    strong {
      margin: 0 -3px 0 0;
      position: relative;
      top: -6px;
      font-family: $rb;
      font-size: 150px;
      font-weight: 400;
      line-height: 100px;
      color: #24fc89;
      display: inline-block; } }

  &__date {
    position: relative;
    font-size: 28px;
    line-height: 1.2;
    margin: 6px 0 0;

    span {
      display: inline-block;
      position: relative;
      margin: 0 auto;
      padding: 0 0 0 0px;

      &:after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: -2px;
        top: 53%;
        background: $themeColor;
        z-index: -1; } } }

  @include md {
    top: 88px;

    &__mon {
      font-size: 36px; }

    &__date {
      font-size: 24px; } }

  @include temp {
    top: 0;
    right: 0;
    position: relative;
    display: flex;
    width: 100%;
    max-width: 200px;
    margin: 140px auto 0;
    justify-content: space-between;

    &__mon {
      line-height: 1; }

    &__date {
      line-height: 1; } }

  @include sm {
    max-width: 160px;
    position: relative;
    margin: 200px auto 0;
    left: -80px;

    &__wrap {
      position: relative;
      top: -4px;
      margin: 0 0 0 auto; }

    &__mon {
      font-size: 28px;

      strong {
        color: #000; } }

    &__date {
      font-size: 22px; } }

  @include xs {
    margin: Min(100%, 136px) 10px 24px;
    max-width: none;
    width: auto;
    left: 0;

    &__mon {
      font-size: 36px;

      strong {
        font-size: 125px;
        line-height: 72px; } } } }

.clock {
  position: absolute;
  top: 80px;
  padding-right: 80px;
  right: -80px;
  width: 270px;
  height: 248px;
  background: url(../img/decor_clock.png) center center no-repeat;
  background-size: cover; }

.timer {
  font-size: 36px;
  font-family: $rb;
  width: 250px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  position: absolute;
  right: 80px;
  top: 113px;
  height: 76px;
  background: linear-gradient(to right, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%); }
