.s-program .section__title { color: #fff; }

.program { display: grid; grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); gap: 20px; color: #fff; margin-top: 60px; }

@media only screen and (max-width: 768px) { .program { margin-top: 30px; } }

.program__item { display: flex; flex-direction: column; padding: 50px 40px 50px 40px; border: 2px solid #24fc89; background: transparent; transition: all ease 0.1s; }

@media only screen and (max-width: 768px) { .program__item { align-items: center; text-align: center; padding: 20px 30px; } }

.program__img-box { display: block; width: 50px; height: 50px; margin: 0 0 18px; }

.program__img-box img { display: block; width: 100%; height: 100%; object-fit: contain; fill: #fff; filter: invert(0%); transition: all ease 0.1s; }

.program__title { font-size: 24px; transition: all ease 0.1s; line-height: 1.18; }

.remote-switch { font-size: 12px; position: sticky; top: 12px; display: flex; height: 58px; align-items: center; padding: 0; background: transparent; pointer-events: auto; margin: 0 0 0 auto; border: 2px solid #171717; width: max-content; }

@media only screen and (max-width: 890px) { .remote-switch { height: 50px; margin: 10px auto 0 0; } }

.remote-switch__text { opacity: 0.6; font-size: 13px; text-decoration: none; color: #171717; height: 100%; padding: 0 10px; display: flex; font-weight: 700; align-items: center; }

@media only screen and (max-width: 890px) { .remote-switch__text { font-size: 12px; } }

.remote-switch__text:first-child { padding: 0 10px 0 20px; }

@media only screen and (max-width: 890px) { .remote-switch__text:first-child { padding: 0 10px 0 10px; } }

.remote-switch__text:last-child { padding: 0 20px 0 10px; }

@media only screen and (max-width: 890px) { .remote-switch__text:last-child { padding: 0 10px 0 10px; } }

.remote-switch__text:not(.active):hover { color: #171717; opacity: 1; }

.remote-switch__text.active { opacity: 1; }

.remote-switch__switch { position: relative; width: 36px; height: 12px; border-radius: 6px; background: rgba(0, 0, 0, 0.2); cursor: pointer; }

@media only screen and (max-width: 890px) { .remote-switch__switch { width: 22px; height: 10px; } }

.remote-switch__switch:after { content: ''; position: absolute; left: -3px; top: -3px; width: 18px; height: 18px; border-radius: 50%; background: #171717; }

@media only screen and (max-width: 480px) { .remote-switch__switch:after { width: 14px; height: 14px; top: -2px; left: -2px; } }

.remote-switch__switch.active:after { left: auto; right: -3px; }

@media only screen and (max-width: 480px) { .remote-switch__switch.active:after { left: auto; right: -2px; } }

.popup-subscription { --bgColor: #171717; --textColor: #fff; --themeColor: #18db73; position: fixed; right: 0; bottom: 0; width: 375px; background: var(--bgColor); z-index: 11; color: var(--textColor); display: flex; flex-direction: column; transition: all ease 0.2s; opacity: 0; visibility: hidden; }

@media only screen and (max-width: 320px) { .popup-subscription { width: 100%; } }

.popup-subscription.done { --bgColor: #18db73; --textColor: #171717; --themeColor: #171717; }

.popup-subscription.done .popup-subscription__content { display: none; }

.popup-subscription.done .popup-subscription__content.done { display: flex; }

.popup-subscription.visible { display: flex; opacity: 1; visibility: visible; }

.popup-subscription.closed { opacity: 0; visibility: hidden; }

.popup-subscription:after { content: ''; position: absolute; left: 0; top: 0; width: 16px; height: 16px; background: var(--themeColor); z-index: 1; }

.popup-subscription__close { position: absolute; top: 32px; right: 32px; height: 24px; width: 24px; cursor: pointer; }

.popup-subscription__close:before, .popup-subscription__close:after { content: ''; position: absolute; left: 50%; top: 50%; width: 20px; height: 2px; background: var(--textColor); transform: translate(-50%, -50%) rotate(-45deg); }

.popup-subscription__close:after { transform: translate(-50%, -50%) rotate(45deg); }

.popup-subscription__close:hover:before, .popup-subscription__close:hover:after { background: var(--themeColor); }

.popup-subscription__content { padding: 50px 32px 32px; display: flex; flex-direction: column; }

.popup-subscription__content.done { display: none; --bgColor: #18db73; --textColor: #171717; --themeColor: #171717; color: var(--textColor); background: var(--bgColor); }

.popup-subscription__title { font-size: 24px; font-weight: 900; line-height: 1.32; text-transform: uppercase; }

.popup-subscription__descr { font-size: 12px; line-height: 1.68; margin: 4px 0 0; }

.popup-subscription__descr a { color: var(--textColor); }

.popup-subscription__form { position: relative; margin: auto 0 0; }

.popup-subscription__label { font-size: 12px; line-height: 1.68; margin: 10px 0 0; }

.popup-subscription__input { outline: none; border: none; border-bottom: 2px solid rgba(255, 255, 255, 0.3); background: transparent; width: 100%; height: 40px; color: var(--textColor); font-weight: 400; transition: all ease 0.1s; font-size: 12px; font-weight: 400; }

.popup-subscription__input-webkit-autofill:focus { background: #f00; }

.popup-subscription__input::placeholder { color: var(--textColor); opacity: 0.3; }

.popup-subscription__input:focus { border-bottom-color: var(--themeColor); }

.popup-subscription__input:focus + .popup-subscription__submit { color: var(--themeColor); }

.popup-subscription__submit { font-weight: 700; color: var(--textColor); background: transparent; transition: all ease 0.1s; font-size: 16px; padding: 0 25px; height: 48px; border-radius: 24px; border: 1px solid var(--textColor); margin: 0 0 0 auto; transition: all ease 0.2s; text-transform: uppercase; cursor: pointer; font-weight: 700; }

.popup-subscription__submit:hover { background: var(--themeColor); border-color: var(--themeColor); color: var(--bgColor); }

.popup-subscription .mc-field-group { margin: 16px 0 0; }

.popup-subscription__btns { margin: 24px 0 0; display: flex; align-items: flex-end; }
