.hero {
	position: relative;
	min-height: 100vh;
	//min-height: calc(var(--vh, 1vh) * 100)
	padding: 30px 0 80px;
	overflow: hidden;
	display: flex;
	justify-content: center;
	align-items: center;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	//background-image: url(../img/hero-overlay.png)
	background-color: #1e2220;
	z-index: 1;
	pointer-events: none;
	color: $white;

	&.hero_inner {
		min-height: 70vh; }

	// &:after
	// 	content: ''
	// 	+coverdiv
	// 	z-index: 1
	// 	background-color: #000
	// 	opacity: 0.35
	// &:before
	// 	content: ''
	// 	+coverdiv
	// 	z-index: 1
	// 	background-image: url(../img/hero-overlay.png)
	// 	background-position: center
	// 	background-repeat: no-repeat
	// 	background-size: cover
	@include xl {
		padding: 45px 0 105px; }
	@include md {
		padding: 60px 0 80px; }
	@include sm {
		padding: 20px 0 60px; }
	@include r(380) {
		@include rhmin(600) {
			padding: 20px 0 40px!important;
			.hero__logo, .h-list {
				margin-bottom: 25px; } } }
	&__video {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		z-index: -1;
		@include r(899) {
			display: none; } }

	&__bg {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		z-index: -1;
		// background: url(../img/screens-done-op.svg) center center no-repeat
		background-size: cover; }

	&__inner {
		width: 1566px;
		//margin: 0 auto
		padding: 40px 40px;
		position: relative;
		z-index: 2;
		pointer-events: none;
		@include r(1460) {
			padding: 30px 80px; }
		@include r(1279) {
			padding: 20px 40px; }
		@include sm {
			padding: 0 20px; }
		@include xs {
			padding: 0 15px; } }

	&__logo {
		width: 283px;
		margin: 0 auto 60px;
		@include xl {
			margin: 0 auto 50px;
			width: 208px; }
		@include md {
			margin: 0 auto 32px; }
		@include xs {
			margin: 30px auto 32px;
			width: 188px;
			height: auto; } }
	&__suptitle {
		font-size: 18px;
		margin-bottom: 52px;
		text-align: center;
		line-height: 1.36;
		@include xl {
			font-size: 18px;
			margin-bottom: 42px; }
		@include md {
			margin-bottom: 28px; }
		@include xs {
			font-size: 15px;
			margin: 0 -10px 13px; } }
	&__title {
		font-weight: 800;
		font-size: 80px;
		line-height: 1.1;
		text-align: center;
		margin-bottom: 42px;
		@include xxl {
			font-size: 64px; }

		@include xl {
			font-size: 53px;
			line-height: 1.11;
			margin-bottom: 34px;
			margin-left: -30px;
			margin-right: -30px; }
		@include md {
			font-size: 7.2vw;
			line-height: 1;
			margin-bottom: 32px; }
		@include sm {
			margin-bottom: 20px;
			margin-left: 0;
			margin-right: 0; }
		@include xs {
			font-size: 30px;
			letter-spacing: 1.27px;
			margin: 0 auto 20px;
			line-height: 1.16;
			max-width: calc(100% - 5px); } }
	&__btn {
		//max-width: 283px
		//margin: 0 auto
		display: flex;
		justify-content: center;
		flex-wrap: wrap;

		.btn {
			pointer-events: auto;
			&:not(:first-child) {
				margin: 0 0 0 20px; } }

		@include sm {
			flex-direction: column;

			.btn {
				margin: 10px auto 0;

				&:not(:first-child) {
					margin: 10px auto 0; } } } }

	&__partner {
		position: absolute;
		right: 10px;
		top: 0;
		font-size: 11px;
		line-height: 1.4;
		pointer-events: auto;

		@include xl {
			font-size: 14px;
			right: 20px; }

		@include md {
			display: flex;
			flex-direction: column;
			align-items: center;
			position: relative;
			right: 0;
			margin: 20px auto; } }

	&__partners {
		display: flex;
		margin-top: 4px;

		@include xl {
			margin-top: 5px; } }

	&__partner-img {
		display: flex;
		align-items: center;
		justify-content: center;

		&:not(:first-child) {
			margin: 0 0 0 20px;

			@include sm {
				margin: 0 0 0 10px; } }

		img {
			height: 60px;
			width: auto; } }

	&__timer {
		position: absolute;
		padding: 0 4vw;
		right: 0;
		left: 0;
		bottom: 1vw;
		font-size: 15vw;
		font-weight: 600;
		color: $themeColorD;
		opacity: .2;
		line-height: 1;
		white-space: nowrap;

		.live {
			text-align: center; } }

	&_live {
		align-items: flex-start;
		height: 150px;
		min-height: 0;
		padding: 20px 0;

		@include xl {
			padding: 20px 0; }
		@include md {
			padding: 20px 0;
			height: 120px; }
		@include sm {
			padding: 10px 0; }
		@include xs {
			height: auto;
			padding: 10px 0 !important; }


		.container {
			position: relative;
			z-index: 2;
			pointer-events: auto;
			width: 100%;
			height: 100%;
			padding: 0 80px;

			@include sm {
				padding: 0 20px; } }

		.hero {
			&__bg {
				z-index: -1;
				background: url(../img/screens-done-op.svg) center top no-repeat;
				background-size: cover; }

			&__inner {
				position: relative;
				width: 100%;
				height: 100%;
				padding: 0;
				margin: 0;
				display: flex;
				justify-content: space-between;

				@include md {
					margin: 0; }

				@include xs {
					flex-direction: column-reverse;
					align-items: center; } }

			&__date {
				font-size: 18px;
				margin: auto 0;

				@include md {
					font-size: 14px; }

				@include xs {
					font-size: 12px;
					margin: 15px 0 10px; } }

			&__logo {
				width: 240px;
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				margin: 0;

				@include md {
					width: 200px;
					margin: 0px auto 0; }

				@include xs {
					position: relative;
					left: 0;
					top: 0;
					transform: translate(0, 0);
					width: 160px;
					margin: 10px auto 0; } }

			&__partner {
				position: relative;
				height: auto;
				margin: auto 0 auto auto;
				top: 0;
				right: 0;

				@include xl {
					font-size: 10px; }

				@include md {
					margin: auto 0 auto;

					img {
						height: 45px; } }

				@include xs {
					display: none; }

				img {
					height: 65px; } }

			&__partners {
				@include md {
					margin: 0; } }



			&__partner-text {
				@include sm {
					display: none; } } } } }
